import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { Checkbox, EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, CodigoReceitaFinanceira } from '../../../../models/dtos/mlf/codigoReceitaFinanceira';

interface CodigoReceitaFinanceiraEditViewProps extends EditViewProps<CodigoReceitaFinanceira> {}

const CodigoReceitaFinanceiraEditView: FC<CodigoReceitaFinanceiraEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CodigoReceitaFinanceira>({
    defaultValues: { ...selectedItem, multiplos: true },
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Código de Receita Auferida por Instituições Financeiras" wikiId={2513}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 5 } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline formProps={formProps} id="descricao" isPrependLabel={true} label="Descrição" />
          </Col>
        </Row>
        <Row>
          <Col style={{ marginBottom: '14px' }}>
            <Checkbox id="instituicaoFinanceira" formProps={formProps} label="Instituição Financeira" />
          </Col>
        </Row>
        <Row>
          <Col style={{ marginBottom: '14px' }}>
            <Checkbox id="segurosPrivados" formProps={formProps} label="Seguros Privados" />
          </Col>
        </Row>
        <Row>
          <Col style={{ marginBottom: '14px' }}>
            <Checkbox id="previdenciaComplementar" formProps={formProps} label="Previdência Complementar" />
          </Col>
        </Row>
        <Row>
          <Col style={{ marginBottom: '14px' }}>
            <Checkbox id="capitalizacao" formProps={formProps} label="Capitalização" />
          </Col>
        </Row>
        <Row>
          <Col style={{ marginBottom: '14px' }}>
            <Checkbox id="assistenciaSaude" formProps={formProps} label="Assistência de Saúde" />
          </Col>
        </Row>
        <Row>
          <Col>
            <Checkbox id="multiplos" formProps={formProps} label="Múltiplos" disabled={true} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CodigoReceitaFinanceiraEditView;
